
import app from './firebase';
import firebase from 'firebase';

async function listAllMentors() {
    try {
        let querySnapshot = await app.firestore().collection('mentors').get();
        const mentors = querySnapshot.docs.map((doc) => {
            return { id: doc.id, ...doc.data() }
          })
          return mentors
    } catch (err) {
        console.log(err);
    }
};
/**
 * 
 * Mentors
 * status: 0: init
 *         1: active/approved
 *         2: canceled
 */
async function enroleMentors(uids){
    uids.forEach(async uid => {
        console.log(uid)
        let querySnapshot = await app.firestore().collection('users').doc(uid).get();
        const user = querySnapshot.data()
        let m={firstname:user.firstname,lastname:user.lastname,email:user.email,status:1};
        if(typeof(user.avatar_url)!=="undefined"){
            m.avatar_url=user.avatar_url;
        }
        if(typeof(user.display_name)==="undefined"){
            m.display_name=user.lastname+" "+user.firstname;
        } else {
            m.display_name=user.display_name;
        }
        app.firestore().collection('mentors').doc(uid).set(m);
        app.firestore().collection('users').doc(uid).update({roles:firebase.firestore.FieldValue.arrayUnion("mentor")})
    });
    console.log("done")
}

export {listAllMentors,enroleMentors}