import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import getInitials from 'src/utils/getInitials';
// import {deleteMentors} from '../../../api/mentors.js';
const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];
const status = [
  {value: 0, label: "Init"},{value: 2, label: "active"},{value: 2, label: "canceled"}
];

const sortOptions = [
  {
    value: 'firstname|desc',
    label: 'firstname (newest first)'
  },
  {
    value: 'firstname|asc',
    label: 'firstname (oldest first)'
  },
  {
    value: 'mentorname|desc',
    label: 'mentorname (giảm)'
  },
  {
    value: 'mentorname|asc',
    label: 'mentorname (tăng)'
  }
];

const applyFilters = (mentors, query, filters) => {
  return mentors.filter((mentor) => {
    let matches = true;

    if (query) {
      const properties = ['firstname', 'lastname','mentorname'];
      let containsQuery = false;

      properties.forEach((property) => {
        if ((property in mentor) && mentor[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && mentor[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (mentors, page, limit) => {
  return mentors.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (mentors, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = mentors.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({
  className,
  mentors,
  ...rest
}) => {
  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedMentors, setSelectedMentors] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedMentors([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllMentors = (event) => {
    setSelectedMentors(event.target.checked
      ? mentors.map((mentor) => mentor.id)
      : []);
  };

  const handleSelectOneMentor = (event, mentorId) => {
    if (!selectedMentors.includes(mentorId)) {
      setSelectedMentors((prevSelected) => [...prevSelected, mentorId]);
    } else {
      setSelectedMentors((prevSelected) => prevSelected.filter((id) => id !== mentorId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const handleDelete = () => {
      // deleteMentors(selectedMentors);
    };

  const filteredMentors = applyFilters(mentors, query, filters);
  const sortedMentors = applySort(filteredMentors, sort);
  const paginatedMentors = applyPagination(sortedMentors, page, limit);
  const enableBulkOperations = selectedMentors.length > 0;
  const selectedSomeMentors = selectedMentors.length > 0 && selectedMentors.length < mentors.length;
  const selectedAllMentors = selectedMentors.length === mentors.length;

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField
          label="Sắp xếp"
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
        >
          {sortOptions.map((option) => (
            <option
              key={option.value}
              value={option.value}
            >
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllMentors}
              indeterminate={selectedSomeMentors}
              onChange={handleSelectAllMentors}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
              onClick={handleDelete}
            >
              Hủy vai trò Mentor
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllMentors}
                    indeterminate={selectedSomeMentors}
                    onChange={handleSelectAllMentors}
                  />
                </TableCell>
                <TableCell>
                  firstname
                </TableCell>
                <TableCell>
                  lastname
                </TableCell>
                <TableCell>
                  mentorname
                </TableCell>
                <TableCell>
                  email
                </TableCell>
                <TableCell>
                  uid
                </TableCell>
                <TableCell>
                  status
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedMentors.map((mentor) => {
                let upvotes = null;
                let downvotes = null;
                if("reactions" in mentor){
                  upvotes = mentor.reactions.upvotes
                  downvotes = mentor.reactions.downvotes  
                }
                
                const isMentorSelected = selectedMentors.includes(mentor.id);
                
                if(!("tags" in mentor)){
                  mentor.tags=[""];
                }
                return (
                  <TableRow
                    hover
                    key={mentor.id}
                    selected={isMentorSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isMentorSelected}
                        onChange={(event) => handleSelectOneMentor(event, mentor.id)}
                        value={isMentorSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                            {mentor.firstname}
                      </Box>
                    </TableCell>
                    <TableCell>
                            {mentor.lastname}
                    </TableCell>
                    <TableCell>
                            {mentor.display_name}
                    </TableCell>
                    <TableCell>
                            {mentor.email}
                    </TableCell>
                    <TableCell>
                    {mentor.id}
                    </TableCell>
                    <TableCell>
                    
                    {status[mentor.status].label}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredMentors.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  mentors: PropTypes.array.isRequired
};

Results.defaultProps = {
  mentors: []
};

export default Results;
